import './CardLoading.css';

function CardLoading() {
    return (
        <div className="card__loading">
            <div className="card_loading__left">
                <div className="card_loading__image"></div>
            </div>
            <div className="card_loading__right">
                <div className="card_loading__title"></div>
                <div className="card_loading__paragraph"></div>
                <div className="card_loading__paragraph"></div>
                <div className="card_loading__half__paragraph"></div>
            </div>
        </div>
    );
}

export default CardLoading;